import { WeaponDamage, WeaponHandedness, resolveWeaponHandedness, AttackType } from "./weapon";
import BaseModel from ".";

export default class Projectile extends BaseModel {
    id: string;
    name: string;
    attackType: AttackType;
    handedness: WeaponHandedness;
    isMeleeWeapon: boolean;
    canFlinch: boolean;
    pointCost: number;
    loadoutPointCost: number;
    
    damageNoArmor: WeaponDamage;
    damageLightArmor: WeaponDamage;
    damageMediumArmor: WeaponDamage;
    damageHeavyArmor: WeaponDamage;

    computedDamage: {
        averageDamage: number;
        dps: number;
    }

    speed: {
        draw: number;
        reload: number;
    }

    computedSpeed: {
        attack: number;
    }

    maxAmmo: number;
    projectileSpeed: number;
    gravityScale: number;
    woodDamage: number;
    stoneDamage: number;

    getSortKey() {
        return this.name + 'projectile' + (this.canFlinch ? '1' : '0');
    }

    constructor(data?: string[]) {
        super();
        
        if (data == null) {
            return;
        }

        this.name =       data[0];
        this.attackType = AttackType.Projectile;
        this.handedness = resolveWeaponHandedness(data[1]);    
        this.isMeleeWeapon = data[2].toLowerCase() == 'yes';   
        this.canFlinch = data[3].toLowerCase() == 'yes';        
        this.pointCost =  parseInt(data[4]);    
        this.loadoutPointCost = parseInt(data[5]); 
        this.damageNoArmor = {
            head: parseInt(data[6]),
            torso: parseInt(data[7]),
            legs: parseInt(data[8]),
        };
        this.damageLightArmor = {
            head: parseInt(data[9]),
            torso: parseInt(data[10]),
            legs: parseInt(data[11]),
        };
        this.damageMediumArmor = {
            head: parseInt(data[12]),
            torso: parseInt(data[13]),
            legs: parseInt(data[14]),
        };
        this.damageHeavyArmor = {
            head: parseInt(data[15]),
            torso: parseInt(data[16]),
            legs: parseInt(data[17]),
        };
        this.computedDamage = {
            averageDamage: parseInt(data[18]),
            dps: parseInt(data[19]),
        };
        this.speed = {
            draw: parseInt(data[20]),
            reload: parseInt(data[21]),
        };
        this.computedSpeed = {
            attack: parseFloat(data[22] != '-' ? data[22] : null),
        };
        this.maxAmmo = parseInt(data[23]);
        this.projectileSpeed = parseInt(data[24]);
        this.gravityScale = parseFloat(data[24]);
        this.woodDamage = parseInt(data[24]);
        this.stoneDamage = parseInt(data[24]);
        
        this.generateId();
    }

    private generateId() {
        let handednessStr = '';
        if (this.handedness & WeaponHandedness.One && this.handedness & WeaponHandedness.Two) {
            handednessStr = '3';
        } else if(this.handedness & WeaponHandedness.One) {
            handednessStr = '1';
        } else if (this.handedness & WeaponHandedness.Two) {
            handednessStr = '2';
        }

        this.id = `${this.name.replace(/ /g, '-')}-projectile-${this.canFlinch ? '1' : '0'}${handednessStr}`;
    }
}