
























import Vue from 'vue';
import { Component, Watch, Prop } from 'vue-property-decorator';
import { DisplayWeapon } from '../../ts/view-model/weapon';
import { sortBy } from '../../ts/utils/sort';

@Component
export default class AttributeBars extends Vue {

    @Prop()
    items: any[];

    @Prop()
    allItems: any[];

    // defaults to a function that accesses 'id' property of the item
    @Prop({type: Function, default: (item) => item.id })
    itemKey: Function;

    @Prop()
    displayValue: (item: DisplayWeapon) => number;

    @Prop({type: Function, default: () => true})
    isEnabled: Function;

    @Prop()
    invert: boolean;

    @Prop()
    sort: boolean;

    @Prop()
    global: boolean;

    max = 0;
    displayItems: DisplayWeapon[] = [];

    mounted() {
        this.itemsChanged();
    }
    
    @Watch('items')
    @Watch('sort')
    @Watch('global')
    itemsChanged() {
        const func = this.invert ? Math.min : Math.max;
        this.max = func(
            ...(this.global ? this.allItems : this.items)
                .map(this.displayValue).filter(o => !isNaN(o))
        );

        this.displayItems = this.sort ? sortBy(this.items, this.displayValue, this.invert) : [...this.items];
    }

    formula(item: DisplayWeapon) {
        const value = this.displayValue(item);

        return this.invert ? this.max / value : value  / this.max;
    }

}
